import React,{useEffect} from 'react'
import Mytab from './Mytab'
import { Grid } from 'antd-mobile';
import {connect} from 'react-redux'
const shoucang = (props) => {
    return (
        <div>
             <Grid data={props.shoucang}
        columnNum={2}
        renderItem={(dataItem,idx) => (
          <div style={{ padding: '12.5px'}}>
            <img src={dataItem} style={{ width: '90px', height: '75px'}} alt=""/>
            <div style={{ color: '#888', fontSize: '14px', marginTop: '12px'}}>
        <span>{props.datalist.map((item)=>{
                        if(item.img===dataItem){
                           return `${item.title}￥${item.price}`
               }
              })}</span>
            </div>
            <button onClick={()=>props.dispatch({
                type:'DEL_TODO',
                index:idx
            })} style={{width:'80px',height:'25px',color:'red',backgroundColor:'gray'}}>删除</button>
        </div>
        )}
      />
            <Mytab/>
        </div>
    )
}
//mydata:state.thingsReducer
const mapStateToProps=(state)=>{return{shoucang:state.shoucangReducer,datalist:state.thingsReducer}}; 
export default connect(mapStateToProps)(shoucang)
