import React from 'react'
import Cart from './shoucang'
import List from './List'
import Mine from './Mine'
import Home from './Home'
import Search1 from './Search1'
import {Detail} from './Things' 
import {BrowserRouter as Router,Switch,Route,Redirect} from 'react-router-dom'
import {Provider} from 'react-redux'
import Store from './Store'
import 'antd-mobile/dist/antd-mobile.css';
import login from './login'

const App=()=>{
	return <Provider store={Store}>
	<Router>
		<Switch>
			<Route exact path='/complex-lasttask/' component={Home}/>
			<Route path='/complex-lasttask/cart' component={Cart}/>
			<Route path='/complex-lasttask/list' component={List}/>
			<Route path='/complex-lasttask/my' component={Mine}/>
			<Route path='/complex-lasttask/search1' component={Search1}/>
			<Route path='/complex-lasttask/docu/:type' component={Detail}/>
			<Route path='/complex-lasttask/login' component={login}/>
			<Route render={()=><Redirect to='/complex-lasttask/' component={Home}/>} />
		</Switch>
	</Router>
	</Provider>
}

export default App;