let datalist=[];
const thingsReducer=(state=datalist,action)=>{
    switch(action.type){
        case 'GETDATA':
            //Object.assign({},state,{username:'123'})//拷贝对象,第1个参数表示复制一份新的，如果没有第1个参数表示第2个参数改成第三个参数那样
            //可以写多个参数，表示把这些参数都放进目标参数state
            return action.datalist;
            default:
            return state;
    }
}
export default thingsReducer