import React from 'react';
import {Button,TabBar} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import 'antd-mobile/dist/antd-mobile.css';

class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'redTab',
    };
  }


  render() {
      const {pathname}=this.props.location;
    return (
      <div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="red"
          barTintColor="white"
        >

          {/* 第一页 */}
          <TabBar.Item
            title="首页"
            key="Home"
            icon={<i className='iconfont icon-Home'></i>}
            selectedIcon={ <i className='iconfont icon-home'></i>}
            selected={pathname === '/complex-lasttask/'}
            badge={1}
            onPress={() => {
                this.props.history.push('/complex-lasttask/');
            }}
            data-seed="logId"
          >
          </TabBar.Item>

          {/* 第二页 */}
          <TabBar.Item
            icon={ <i className='iconfont icon-shoucang'></i> }
            selectedIcon={ <i className='iconfont icon-gouwuche'></i> }
            title="收藏"
            key="cart"
            badge={'new'}
            selected={pathname === '/complex-lasttask/cart'}
            onPress={() => {
                this.props.history.push('/complex-lasttask/cart');
            }}
            data-seed="logId1"
          >
          </TabBar.Item>

          {/* 第三页 */}
          <TabBar.Item
            icon={<i className='iconfont icon-74wodedingdan'></i>}
            selectedIcon={<i className='iconfont icon-dingdan'></i>}
            title="订单列表"
            key="Friend"
            dot
            selected={pathname==='/complex-lasttask/list'}
            onPress={() => {
                this.props.history.push('/complex-lasttask/list');
            }}
          >
          </TabBar.Item>
          <TabBar.Item
            icon={<i className='iconfont icon-dibudaohanglan-'></i>}
            selectedIcon={<i className='iconfont icon-wode'></i>}
            title="我的淘宝"
            key="my"
            selected={pathname=== '/complex-lasttask/my'}
            onPress={() => {
              this.props.history.push('/complex-lasttask/my')
            }}
          >
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default withRouter(Mytab)