import React from 'react'
import getlogin from './getlogin'
import {connect} from 'react-redux'

const login = (props) => {
    const log=()=>{
        props.dispatch(getlogin())
        props.history.goBack()
        //console.log(props.username)
      }
    return (
        <div style={{marginLeft:'90px',marginTop:'200px'}}>
            <input type='text' placeholder='请输入用户名' style={{width:'200px',height:'30px'}}/>
            <input type='password' style={{width:'200px',height:'30px',marginTop:'10px',marginBottom:'10px'}}/>
            <button style={{width:'200px',height:'50px'}} onClick={log}>登录</button>
        </div>
    )
}
const mapStateToProps=(state)=>({
    username:state.loginReducer.username
})
export default connect(mapStateToProps)(login)
