import React from 'react';
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'
import 'antd-mobile/dist/antd-mobile.css';
const Dtal=(props)=>{
  const img=()=>{
    for(let i=0;i<props.shoucang.length;i++){
      if(props.shoucang[i]===props.val){
        return 'iconfont icon-shoucang2'
      }
    }
    return 'iconfont icon-shoucang'
  }
  const funlog=(e)=>{
    if(props.username){
      //console.log(img);
      props.dispatch({type:'ADD_TODO',value:props.val})
      e.target.className = 'iconfont icon-shoucang2';
    }else{
      props.history.push('/complex-lasttask/login')
    }
  }
  //console.log(props);
  return (
    <div style={{ position: 'fixed', width: '100%', bottom: 0}}>
         <a className='iconfont icon-dianpu' style={{display:'inline-block',marginRight:'25px'}}><div style={{fontSize:12}}>店铺</div></a>
         <a className='iconfont icon-kefu' style={{display:'inline-block',marginRight:'25px'}}><div style={{fontSize:12}}>客服</div></a>
         <a className={img()} style={{display:'inline-block',marginRight:'35px'}} onClick={(e)=>funlog(e)}><div style={{fontSize:12}}>收藏</div></a>
         <div style={{display:'inline-block',heigth:'30px'}}><div><button style={{width:'83px',height:'30px',fontSize:'13px',marginTop:'5px',borderTopLeftRadius:'13px',borderBottomLeftRadius:'13px',backgroundColor:'orange'}}>加入购物车</button>
          <button style={{width:'83px',height:'30px',borderTopRightRadius:'13px',borderBottomRightRadius:'13px',background:'orangered'}}>立即购买</button></div>
         </div>
    </div>
  );
}
const mapStateToProps=(state)=>({
  username:state.loginReducer.username,
  shoucang:state.shoucangReducer
})
export default connect(mapStateToProps)(withRouter(Dtal))