let shoucang=[];
function adelshou(state=shoucang,action){
    //添加
    if(action.type=='ADD_TODO'){
        //不可变对象
        return [...state,action.value]
    }
    if(action.type=='DEL_TODO'){
        let arr=[...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}
export default adelshou;
