const loginReducer=(state={username:''},action)=>{
    switch(action.type){
        case 'LOGIN':
            //Object.assign({},state,{username:'123'})//拷贝对象,第1个参数表示复制一份新的，如果没有第1个参数表示第2个参数改成第三个参数那样
            //可以写多个参数，表示把这些参数都放进目标参数state
            return {username:action.username};
            default:
            return state;
    }
}
export default loginReducer