import React,{useEffect} from 'react'
import { Grid } from 'antd-mobile';
import {NavLink} from 'react-router-dom'
import {connect} from 'react-redux'
import Dtal from './Dtal'
import getdata from './getdata'
let selectItem;
export const Detail=(props)=>{
  return (
  <div>
    <div style={{position:'relative'}}>
      <img src={selectItem} style={{width:'100%', height:"100%"}}/>
      <button style={{position:'absolute',left:'10px',top:'15%',width:'55px',height:'55px',borderRadius:'30px',background:'rgba(125,125,125,0.4)'}}
      onClick={()=>{props.history.push('/complex-lasttask/')}}>&lt;</button>
    </div>
    <Dtal val={selectItem}/>
  </div>)
}
const Things = (props) => {

  useEffect(() => {
    props.dispatch(getdata())
  }, [])
  
    return(<div>
      <div style={{height:'40px',lineHeight:'40px',textAlign:'center',color:'red',fontSize:'20px'}}>~你可能喜欢~</div>
      <Grid data={props.datalist}
        columnNum={2}
        renderItem={dataItem => (
          <div style={{ padding: '12.5px'}}>
            <NavLink to={`/complex-lasttask/docu/${dataItem.img}`}><img src={dataItem.img} style={{ width: '90px', height: '75px'}} alt="" onClick={()=>{selectItem=dataItem.img}}/></NavLink>
            <div style={{ color: '#888', fontSize: '14px', marginTop: '12px' }}>
            <span>{dataItem.title}<span style={{color:'red',fontSize:19}}>￥{dataItem.price}</span></span>
            </div>
          </div>
        )}
      />
    </div>)
};
  const mapStateToProps=(state)=>{return{datalist:state.thingsReducer}}; 
  export default connect(mapStateToProps)(Things)