import { Carousel} from 'antd-mobile';
import React from 'react'

class Carou extends React.Component {
  state = {
    data: ['1', '2', '3','4','5'],
    imgHeight: 176,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['TB1XotJXQfb_uJkSnhJSuvdDVXa', 'TB1JNHwKFXXXXafXVXXSutbFXXX', 'TB183NQapLM8KJjSZFBSutJHVXa','TB1X6uHLVXXXXcCXVXXSutbFXXX','TB13xKuLVXXXXcHapXXSutbFXXX'],
      });
    }, 100);
  }
  render() {
    return (
        <Carousel
          autoplay={true}
          infinite
          // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          // afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`https://aecpm.alicdn.com/simba/img/${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}
export default Carou